import { connect } from 'react-redux';
import FileHeaderActions from '../components/FileHeaderActions';
import actions from '../modules/rockhopper/actions';
import fileQueue from '../modules/rockhopper/file-queue'

import { selectFileList, selectFile, selectFileUploadsThatNeedOverwritePermission, selectFileUploads } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => ({
  file: selectFile(state),
  needsOverwritePermission: selectFileUploadsThatNeedOverwritePermission(state),
  queuedFiles: selectFileUploads(state),
  fileList: selectFileList(state)
}), 
(dispatch, ownProps) => ({ 
  onCancelAll: () => dispatch(actions.fileUpload.cancelAll()),
  onCancel: (path) => dispatch(actions.fileUpload.cancel(path)),
  onConfirm: (path) => dispatch(actions.fileUpload.confirmOverwrite(path)),
  onConfirmAll: () => dispatch(actions.fileUpload.confirmOverwriteAll()),
  onAddFile: (path, data) => fileQueue.addFile(path, data),
  onFileOpen: (path) => dispatch(actions.put.programOpenCmd({ data: [ path ] })),
  // TODO - Add CommandItem that can delete multiple files at once
  onFileDelete: (path) => dispatch(actions.put.programDelete({ data: [ path ] }))
}))(FileHeaderActions);
